.project {
  width: 100%;
  font-family: 'Spoqa Han Sans Neo', 'sans-serif';
  padding-top: 150px;
  background-color: ghostwhite;
  padding: 70px 0% 100px 0%;
}

.project > div:first-of-type {
  padding-left: 5%;
  color: #3a78fe;
  font-size: 1.2rem;
  font-weight: 600;
}

.projectCard {
  display: flex;
  justify-content: center;
  justify-content: space-around;
  max-width: 1000px;
  margin: 80px auto 0px auto;

}

.projectImg {
  width: 420px;
  display: flex;
  align-items: center;
}

.projectImg:nth-of-type(2) {
  justify-content: right;
}

.projectImg a {
  font-size: 0px;
}

.projectImg img {
  height: 280px;;
  box-shadow: 10px 10px 20px 0 #bdbcbc;
  border-radius: 10px;
}

.projectText {
  background-color: rgb(255, 255, 255);
  border: 1px solid rgba(0,0,0,0.2);
  width: 260px;
  border-radius: 10px;
  position: relative;
}

.projectText h3 {
  border-radius: 10px 10px 0 0 ;
  margin: 0;
  background-color:  #3a78fe;
  color: white;
  padding: 5% 10%;
}


.projectText p {
  color: rgb(70, 70, 70);
  text-align: justify;
  padding: 20px 0 20px 0;
  width: 80%;
  margin: auto;
}

.projectText a {
  color: rgb(70, 70, 70);
  margin-left: 10%;
  
}

@media (max-width: 700px) {
  .projectCard {
    margin-top: 40px;
    min-width: auto;
    flex-direction: column-reverse;
    align-items: center;
    gap: 1.5rem;
  }

  .projectCard:nth-of-type(3) {
    flex-direction: column;
  }

  .projectImg {
    justify-content: center;
    width: 80%;
    max-width: 420px;
  }

  .projectImg:nth-of-type(2) {
  justify-content: center;
  }

  .projectImg img {
    width: 100%;
    height: auto;
  }

  .projectText {
    width: 80%;
    max-width: 420px;
  }

  .projectText h3 {
    padding: 3% 10%;
  }

  .projectText a {
    display: block;
    padding-bottom: 20px;
  }

}